<template>
    <el-container>
        <el-header>
            <el-select v-model="state" placeholder="请选择">
                <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <el-button type="primary" @click="reload()" icon="el-icon-search">查询</el-button>
            <el-button type="primary" @click="exp()" icon="el-icon-bottom">导出</el-button>
            <!--<el-button type="primary" @click="reload()" icon="el-icon-top">导入</el-button>-->

            <el-upload style="width: 110px;display: inline-grid;"
                       action=""
                       :on-change="change"
                       :show-file-list="false"
                       :multiple="false"
                       :auto-upload="false">
                <el-button type="primary" icon="el-icon-top">导入</el-button>
            </el-upload>

        </el-header>
        <el-main>
            <el-row>
                <el-col>
                    <el-table
                            :data="tableData"
                            border
                            stripe
                            style="width: 100%;">

                        <el-table-column
                                prop="id"
                                label="订单号">
                        </el-table-column>

                        <el-table-column
                                prop="pName"
                                label="商品名称">
                        </el-table-column>

                        <el-table-column
                                prop="userName"
                                label="推广人名称">
                        </el-table-column>

                        <el-table-column
                                prop="createDate"
                                label="创建时间">
                        </el-table-column>

                        <el-table-column
                                prop="senderName"
                                label="收件人姓名">
                        </el-table-column>

                        <el-table-column
                                prop="senderMobile"
                                label="收件人号码">
                        </el-table-column>

                        <el-table-column
                                prop="address"
                                label="收件人地址">
                        </el-table-column>

                        <el-table-column
                                label="状态" width="80">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.state==1" type="success">已发货</el-tag>
                                <el-tag v-if="scope.row.state==2" type="info">已签收</el-tag>
                                <el-tag v-if="scope.row.state==0" type="warning">待发货</el-tag>
                            </template>
                        </el-table-column>

                        <el-table-column
                                prop="expressName"
                                label="快递名称">
                        </el-table-column>

                        <el-table-column
                                prop="expressNumber"
                                label="快递单号">
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row>
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="countSize">
                </el-pagination>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        data() {
            return {
                fileName: "",
                state: '-1',
                options: [{
                    value: '-1',
                    label: '全部'
                }, {
                    value: '0',
                    label: '待发货'
                }, {
                    value: '1',
                    label: '已发货'
                }, {
                    value: '2',
                    label: '已签收'
                }],
                tableData: null,
                currentPage: 1,
                countSize: 0,
                pageSize: 1
            }
        },
        mounted() {
            this.reload();
        },
        methods: {
            change(file) {
                if (file.name.toLowerCase().indexOf(".xls") == -1) {
                    this.$message({
                        type: 'error',
                        message: '文件格式只支持 xls'
                    });
                } else {
                    this.fileName = file.name;

                    let formData = new FormData();
                    console.log(file)
                    formData.append('file', file.raw);

                    let config = {
                        headers: {'Content-Type': 'multipart/form-data'}
                    }
                    // 添加请求头
                    this.axios.post('/api/order/upload', formData, config).then(response => {
                        if (response.data.state == 0) {
                            this.fileName = '';
                            this.$message({
                                type: 'success',
                                message: '导入成功'
                            });
                            this.reload();
                        } else {
                            this.$message({
                                type: 'error',
                                message: response.data.message
                            });
                        }
                    })
                }
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.reload();
            },
            reload() {
                this.axios.get("/api/order/page?serchkey=" + this.state + "&page=" + this.currentPage).then((response) => {
                    this.tableData = response.data.data.content;
                    this.currentPage = response.data.data.number + 1;
                    this.countSize = response.data.data.totalElements;
                    this.pageSize = response.data.data.size;
                }).catch((response) => {
                    console.log(response);
                })
            },
            exp() {
                window.location.href = this.axios.defaults.baseURL + "/api/order/exp?serchkey=" + this.state;
            },
        }
    }
</script>

<style scoped>
    .el-select {
        margin-right: 10px;
    }

</style>
